class ThisComponent extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      logoIndex: 0,
      logosLength: 6,
      logoText: "",
      showNav: false
    }
  }

  updateLogoIndex = (newIndex) => {
    if (typeof index === "undefined") {
      let length = this.state.logosLength
      newIndex = this.state.logoIndex + 1
      if (newIndex >= length) {
        newIndex = newIndex - length
      }
    }
    this.setState({
      logoIndex: newIndex
    })
  }

  updateLogoText = () => {
    let pathlist = this.props.location.pathname.split("/")
    this.setState({
      logoText: pathlist[1]
    })
  }

  componentDidMount() {
    this.updateLogoText()
  }

  componentDidUpdate(prevProps) {
    if (this.props.location.pathname !== prevProps.location.pathname) {
      this.updateLogoText()
    }
  }

  render() {
    let { home, loading, standalone, location } = this.props
    let Logo = NLPStudioLogo
    if (
      typeof window !== "undefined" &&
      location.pathname.includes("word") // window.location.hostname.includes("thesaurus") ||
    ) {
      Logo = NLPThesaurusLogo
    } else if (!loading) {
      Logo = NLPDomainsLogo
    }

    return (
      <StyledHeadContainer className={standalone ? "wrapInContainer" : ""}>
        {!!home && <StyledHeadUnder />}
        <StyledHead className="StyledHead " style={{ position: !!home ? "fixed" : "" }}>
          {/*
           * Header Bar
           */}
          <StyledHeader className="StyledHeader content">
            {/*
             * Logo
             */}
            <StyledLogoContainer>
              <h2
                className="StyledLogo"
                onClick={() => {
                  this.updateLogoIndex()
                }}
              >
                {Logo}
              </h2>
            </StyledLogoContainer>

            {/*
             * Contact us link
             */}
            <StyledToplinks>
              <Link to="/contact">contact us</Link>
            </StyledToplinks>

            {/*
             * Hamburger Menu
             */}
            <StyledRightburger
              className="StyledRightburger"
              onMouseOver={() => {
                this.setState({ showNav: true })
              }}
            >
              <FA icon={faBars} className="faBars" />
            </StyledRightburger>
            {/*
             * Top-Right Dropdown Nav Menu
             */}
            <StyledHeaderDropdown className={this.state.showNav ? "visible" : "hidden"}>
              <AccountMenu {...this.props} />
            </StyledHeaderDropdown>
            <StyledHeaderDropdownOverlay
              className={this.state.showNav ? "visible" : "hidden"}
              onMouseOver={() => {
                this.setState({ showNav: false })
              }}
            />
          </StyledHeader>
        </StyledHead>
      </StyledHeadContainer>
    )
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    output_actions: bindActionCreators(output_actions, dispatch),
    input_actions: bindActionCreators(input_actions, dispatch),
    ui_actions: bindActionCreators(ui_actions, dispatch)
  }
}
const mapStateToProps = function (state) {
  return {
    input_str: state.input.input_str,
    input_tld: state.input.input_tld,
    input_first_word: state.input.input_first_word
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(ThisComponent)
